html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 10px;
  height: 100%; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  height: inherit;
  margin: 0;
  font-family: "Verdana", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
  font-weight: 400;
  font-style: normal;
  color: #2e2d2d; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0; }

.textarea {
  display: block;
  width: 700px;
  margin: 0 auto 10px;
  min-height: 150px;
  padding: 10px; }

.keyboard {
  width: 700px;
  margin: 0 auto;
  background-color: #1a1a1a;
  text-align: center; }

.keyboard__key {
  width: 40px;
  height: 40px;
  padding: 0;
  margin: 3px;
  border: none;
  background-color: #333333;
  font-size: 1.3rem;
  text-align: center;
  vertical-align: middle;
  color: #e5e5e5;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  cursor: pointer; }
  .keyboard__key:hover {
    background-color: #e5e5e5;
    color: #333333; }
  .keyboard__key--active, .keyboard__key:active {
    background-color: #0076d7; }
  .keyboard__key--backspace, .keyboard__key--capslock {
    width: 90px; }
  .keyboard__key--backslash {
    width: 60px; }
  .keyboard__key--enter {
    width: 88px; }
  .keyboard__key--shift {
    width: 91px; }
  .keyboard__key--shift-right {
    width: 85px; }
  .keyboard__key--tab {
    width: 70px; }
  .keyboard__key--space {
    width: 229px; }

.info__title,
.info__description {
  max-width: 50%;
  margin-right: auto;
  margin-left: auto;
  text-align: center; }

.info__description {
  font-size: 1.6rem; }
